import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            isReject: false,
            appeal: '药吃完了',
            reject: '',
            info: this.$store.state.pInfo
        }
    },
    methods: {
        toReject(){
            this.$post('ConsultAPI/Consult/UpdateInquiryRecordStatus',{
                OperatorRole: 2,
                OperatorId: this.$store.state.userInfo.Data.User.DoctorId,
                Status: 2,
                Remark: this.reject,
                Id: this.info.Id
            }).then(o => {
                this.$toast('已拒绝该问诊预约')
            })
        }
    }
}



